import React, { useEffect, useState } from 'react';

// material-ui
import {
    Avatar,
    CardContent,
    CardActions,
    Card,
    Stack,
    Typography,
    Container,
    Chip,
    Grid,
    Button,
    TextField,
    Dialog,
    DialogActions,
    DialogContent,
    DialogContentText,
    DialogTitle,
    Paper,
    ListItemAvatar,
    ListItemButton,
    ListItemSecondaryAction,
    Tooltip
} from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import ArrowUpwardIcon from '@mui/icons-material/ArrowUpward';
import ArrowDownwardIcon from '@mui/icons-material/ArrowDownward';

import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import dayjs from 'dayjs';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { DatePicker } from '@mui/x-date-pickers';
import { FormControl, FormControlLabel, IconButton, MenuItem, Radio, RadioGroup, Select } from '@mui/material';
import { useLocation } from 'react-router';
import { apiBaseURL } from 'src/config-global';
import { toast } from 'react-toastify';
import axios from 'axios';
import { Link } from 'react-router-dom';

// ==============================|| SAMPLE PAGE ||============================== //

const numToColor = new Map([
    [1, 'primary'],
    [2, 'warning'],
    [3, 'error'],
    [4, 'info']
]);

const numToLabelTimeframe = new Map([
    [1, 'Short Term'],
    [2, 'Long Term'],
    [3, 'Urgent']
]);
const numToLabelImpact = new Map([
    [1, 'Low'],
    [2, 'Medium'],
    [3, 'Urgent']
]);

const ProjectView = () => {
    const location = useLocation();
    const query = new URLSearchParams(location.search);
    const _id = query.get('id');
    const title = query.get('title');
    if (!_id) {
        return <>Bad Request</>;
    }

    const [open, setOpen] = React.useState(false);
    const handleClickOpen = () => {
        setOpen(true);
    };
    const handleClose = () => {
        setOpen(false);
    };

    const handleClick = (e) => {
        console.info(e.target.value);
    };

    const [datePicker, setDatePicker] = React.useState(false);
    const [goalDate, setGoalDate] = React.useState(null);

    const [radioValue, setRadioValue] = React.useState(false);
    const handleRadioChange = (event) => {
        setRadioValue(event.target.value);
    };

    const [order, setOrder] = useState(1);
    const [sorting, setSorting] = useState({
        false: 'createdOn',
        true: 'createdOn'
    });

    const [newGoal, setNewGoal] = useState({
        name: '',
        desc: '',
        timeframe: 1,
        impact: 1,
        custom: null
    });

    const handleInput = (field, value) => () => {
        if (field === 'timeframe') setGoalDate(null);
        const e = { target: { value } };
        handleGoalInput(field)(e);
    };

    const handleGoalInput = (field) => (e) => {
        setNewGoal((prev) => ({
            ...prev,
            [field]: e.target.value
        }));
    };

    const handleDialogSubmit = async () => {
        if (newGoal.timeframe === 4) {
            newGoal.custom = new Date(goalDate);
        } else {
            delete newGoal.custom;
        }
        const formData = {
            ...newGoal,
            projectId: _id
        };
        // console.log(formData);
        const res = await axios.post(`${apiBaseURL}/project/goal`, { formData });
        if (res.data === 'Created') {
            fetchGoals();
            handleClose();
            setNewGoal({
                name: '',
                desc: '',
                timeframe: 1,
                impact: 1,
                custom: null
            });
            toast.success('Goal Added');
        } else {
            toast.error('Cannot add goal, try again');
        }
    };

    const [goals, setGoals] = useState([]);
    const fetchGoals = () => {
        axios.get(`${apiBaseURL}/project/goal/${_id}?status=${radioValue}&sortBy=${sorting[radioValue]}&order=${order}`).then((res) => {
            setGoals(res.data);
        });
    };

    useEffect(() => {
        fetchGoals();
    }, [radioValue, sorting, order]);

    return (
        <>
            <Dialog
                open={open}
                onClose={handleClose}
                fullWidth
                maxWidth="sm"
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title" variant="h3">
                    Add a new goal
                </DialogTitle>
                <DialogContent>
                    <TextField
                        id="outlined-basic"
                        fullWidth
                        sx={{ mb: 2, mt: 2 }}
                        label="Goal name"
                        variant="outlined"
                        value={newGoal.name}
                        onChange={handleGoalInput('name')}
                    />
                    <TextField
                        id="outlined-basic"
                        fullWidth
                        sx={{ mb: 2 }}
                        label="Brief description"
                        multiline
                        rows={4}
                        variant="outlined"
                        value={newGoal.desc}
                        onChange={handleGoalInput('desc')}
                    />

                    <Typography variant="subtitle1" sx={{ mb: 1 }}>
                        Select a timeframe
                    </Typography>
                    <Stack direction="row" spacing={1}>
                        <Chip
                            variant={newGoal.timeframe === 3 ? 'filled' : 'outlined'}
                            color={'error'}
                            label="Urgent"
                            onClick={handleInput('timeframe', 3)}
                        />
                        <Chip
                            variant={newGoal.timeframe === 2 ? 'filled' : 'outlined'}
                            color={'warning'}
                            label="Long Term"
                            onClick={handleInput('timeframe', 2)}
                        />
                        <Chip
                            variant={newGoal.timeframe === 1 ? 'filled' : 'outlined'}
                            color={'primary'}
                            label="Short Term"
                            onClick={handleInput('timeframe', 1)}
                        />
                        {!datePicker && (
                            <Chip
                                variant={newGoal.timeframe === 4 ? 'filled' : 'outlined'}
                                color="info"
                                label={goalDate ? new Date(goalDate).toLocaleDateString() : 'Custom'}
                                onClick={(e) => setDatePicker(true)}
                            />
                        )}
                        {datePicker && (
                            <LocalizationProvider dateAdapter={AdapterDayjs}>
                                <DatePicker
                                    disablePast
                                    displayStaticWrapperAs="desktop"
                                    openTo="year"
                                    value={goalDate}
                                    onChange={(newValue) => {
                                        setGoalDate(newValue);
                                    }}
                                    onClose={(e) => {
                                        if (!goalDate) return;
                                        setNewGoal((prev) => ({
                                            ...prev,
                                            timeframe: 4
                                        }));
                                        setDatePicker(false);
                                    }}
                                    renderInput={(params) => <TextField {...params} />}
                                />
                            </LocalizationProvider>
                        )}
                    </Stack>
                    <br />
                    <Typography variant="subtitle1" sx={{ mb: 1 }}>
                        Define impact
                    </Typography>
                    <Stack direction="row" spacing={1}>
                        <Chip
                            variant={newGoal.impact === 3 ? 'filled' : 'outlined'}
                            color="error"
                            label="Urgent"
                            onClick={handleInput('impact', 3)}
                        />
                        <Chip
                            variant={newGoal.impact === 2 ? 'filled' : 'outlined'}
                            color="warning"
                            label="Medium"
                            onClick={handleInput('impact', 2)}
                        />
                        <Chip
                            variant={newGoal.impact === 1 ? 'filled' : 'outlined'}
                            color="primary"
                            label="Low"
                            onClick={handleInput('impact', 1)}
                        />
                        {/* <Chip label="$" onClick={handleClick} /> */}
                    </Stack>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose}>Cancel</Button>
                    <Button onClick={handleDialogSubmit} autoFocus>
                        Create goal
                    </Button>
                </DialogActions>
            </Dialog>

            <Grid container rowSpacing={4.5} columnSpacing={2.75}>
                <Grid item xs={12}>
                    <Typography sx={{ float: 'left' }} variant="h3">
                        {location.state?.title ? location.state?.title : title ? title : '<Project Title>'}
                    </Typography>
                    <Button variant="contained" startIcon={<AddIcon />} onClick={handleClickOpen} sx={{ float: 'right' }}>
                        Add a new goal
                    </Button>
                </Grid>
                <Grid item xs={12}>
                    <FormControl>
                        <RadioGroup
                            row
                            aria-labelledby="demo-row-radio-buttons-group-label"
                            name="row-radio-buttons-group"
                            value={radioValue}
                            onChange={handleRadioChange}
                        >
                            <FormControlLabel value={false} control={<Radio />} label="In-Progress" />
                            <FormControlLabel value={true} control={<Radio />} label="Completed" />
                        </RadioGroup>
                    </FormControl>
                    <FormControl size="small" sx={{ float: 'right' }}>
                        <Select
                            id="demo-simple-select"
                            value={sorting[radioValue]}
                            onChange={(e) => {
                                setSorting((prev) => ({
                                    ...prev,
                                    [radioValue]: e.target.value
                                }));
                            }}
                        >
                            <MenuItem value="createdOn">Time Created</MenuItem>
                            <MenuItem value="impact">Impact</MenuItem>
                            <MenuItem value="timeframe">Time Frame</MenuItem>
                        </Select>
                    </FormControl>
                    <IconButton sx={{ float: 'right', borderRadius: '50%' }} onClick={() => setOrder(order > 0 ? -1 : 1)}>
                        {order > 0 ? <ArrowDownwardIcon /> : <ArrowUpwardIcon />}
                    </IconButton>
                    <br />
                    <br />
                    <TableContainer component={Paper}>
                        <Table aria-label="simple table">
                            <TableHead>
                                <TableRow>
                                    <TableCell>Goal Name</TableCell>
                                    <TableCell align="center">Timeframe</TableCell>
                                    <TableCell align="center">Impact</TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {goals.map((item) => {
                                    const timeframe =
                                        item.timeframe === 4
                                            ? new Date(item.custom).toLocaleDateString()
                                            : numToLabelTimeframe.get(item.timeframe);
                                    return (
                                        <>
                                            <TableRow key={item} sx={{ '&:last-child td, &:last-child th': { border: 0 } }}>
                                                <TableCell component="th" scope="row">
                                                    <Link
                                                        style={{ textDecoration: 'none' }}
                                                        to={`/dashboard/projectgoal?goalId=${item._id}&projectId=${item.projectId}`}
                                                    >
                                                        {item.name}
                                                    </Link>
                                                </TableCell>
                                                <TableCell align="center">
                                                    <Chip color={numToColor.get(item.timeframe)} label={timeframe} />
                                                </TableCell>
                                                <TableCell align="center">
                                                    <Chip color={numToColor.get(item.impact)} label={numToLabelImpact.get(item.impact)} />
                                                </TableCell>
                                            </TableRow>
                                        </>
                                    );
                                })}
                            </TableBody>
                        </Table>
                    </TableContainer>
                </Grid>
            </Grid>
        </>
    );
};

export default ProjectView;
